
"use client";

import React, { createContext, useContext, useCallback } from "react";
import { useSession, signIn as nextAuthSignIn, signOut as nextAuthSignOut, SessionProvider } from "next-auth/react";
import { useRouter } from "next/navigation";
import type { Role } from "@/types"; // Pastikan path ini benar
import { useToast } from "@/hooks/use-toast";
import { ROUTES, ROLES } // Pastikan ROLES diimpor jika digunakan
from "@/lib/constants"; 
import type { User as AuthUser } from "next-auth"; // Menggunakan tipe User dari next-auth

// Definisikan tipe untuk user dalam sesi, sesuaikan dengan next-auth.d.ts
interface SessionUser extends AuthUser {
  id: string;
  role: Role;
  email: string;
  full_name?: string | null;
  phone?: string | null;
  address?: string | null;
  birth_date?: string | null; // ISO string date
  bio?: string | null;
  nis?: string | null;
  nip?: string | null;
  join_date?: string | null; // ISO string date
  avatar_url?: string | null;
}

interface AuthContextType {
  user: SessionUser | null;
  login: (email: string, passwordAttempt: string) => Promise<boolean>;
  register: (email: string, passwordAttempt: string, fullName?: string) => Promise<boolean>;
  logout: () => Promise<void>;
  updateUserProfile: (data: Partial<Omit<SessionUser, 'id' | 'email' | 'role' | 'nis' | 'nip' | 'join_date'>>) => Promise<boolean>;
  isLoading: boolean; // status dari useSession
  // users: SessionUser[]; // Untuk manajemen pengguna, ini mungkin memerlukan pendekatan berbeda dengan NextAuth
  // verifyUserEmail: (userId: string) => void;
  // createUser: (userData: Omit<SessionUser, 'id' | 'isVerified'> & { password?: string }) => SessionUser | null;
  // updateUserRole: (userId: string, newRole: Role) => void;
  // deleteUser: (userId: string) => Promise<void>;
  // requestPasswordReset: (email: string) => Promise<boolean>;
  // resetPassword: (token: string, newPassword: string) => Promise<boolean>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

function AuthProviderContent({ children }: { children: React.ReactNode }) {
  const { data: session, status, update: updateSession } = useSession();
  const router = useRouter();
  const { toast } = useToast();
  const isLoading = status === "loading";

  const login = useCallback(async (email: string, passwordAttempt: string): Promise<boolean> => {
    try {
      const result = await nextAuthSignIn("credentials", {
        redirect: false,
        email,
        password: passwordAttempt,
      });

      if (result?.error) {
        toast({ title: "Login Gagal", description: result.error || "Email atau kata sandi salah.", variant: "destructive" });
        return false;
      }
      if (result?.ok && session?.user) {
         toast({ title: "Login Berhasil", description: `Selamat datang kembali, ${session.user.name || session.user.email}!` });
        // Pengalihan ditangani oleh middleware atau halaman page.tsx
        router.refresh(); // Refresh untuk memastikan sesi terbaru diambil
        return true;
      }
      return false;
    } catch (error) {
      console.error("Login error:", error);
      toast({ title: "Login Gagal", description: "Terjadi kesalahan tak terduga.", variant: "destructive" });
      return false;
    }
  }, [router, toast, session]);

  const register = useCallback(async (email: string, passwordAttempt: string, fullName?: string): Promise<boolean> => {
    try {
      const response = await fetch('/api/auth/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password: passwordAttempt, fullName }),
      });

      const data = await response.json();

      if (!response.ok) {
        toast({ title: "Pendaftaran Gagal", description: data.error || "Tidak dapat mendaftar.", variant: "destructive" });
        return false;
      }
      
      toast({ title: "Pendaftaran Berhasil", description: "Silakan login dengan akun baru Anda." });
      // Opsional: login otomatis setelah registrasi
      // await login(email, passwordAttempt); 
      router.push(ROUTES.LOGIN);
      return true;

    } catch (error) {
      console.error("Register error:", error);
      toast({ title: "Pendaftaran Gagal", description: "Terjadi kesalahan jaringan atau server.", variant: "destructive" });
      return false;
    }
  }, [router, toast]);

  const logout = useCallback(async () => {
    await nextAuthSignOut({ redirect: false });
    router.push(ROUTES.LOGIN);
    toast({ title: "Berhasil Keluar", description: "Anda telah berhasil keluar." });
  }, [router, toast]);

  const updateUserProfile = useCallback(async (data: Partial<Omit<SessionUser, 'id' | 'email' | 'role' | 'nis' | 'nip' | 'join_date'>>): Promise<boolean> => {
    if (!session?.user?.id) {
      toast({ title: "Error", description: "Anda harus login untuk memperbarui profil.", variant: "destructive" });
      return false;
    }
    try {
      const response = await fetch('/api/profile/update', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      });
      const result = await response.json();
      if (!response.ok) {
        toast({ title: "Pembaruan Gagal", description: result.error || "Tidak dapat memperbarui profil.", variant: "destructive" });
        return false;
      }
      toast({ title: "Profil Diperbarui", description: "Informasi profil Anda telah berhasil disimpan." });
      
      // Memperbarui sesi NextAuth dengan data baru
      await updateSession({
        ...session,
        user: {
          ...session.user,
          ...result.user, // result.user harus berisi data profil yang diperbarui dari server
          name: result.user.full_name || session.user.email, // pastikan name juga diperbarui
        }
      });
      return true;
    } catch (error) {
      console.error("Update profile error:", error);
      toast({ title: "Pembaruan Gagal", description: "Terjadi kesalahan.", variant: "destructive" });
      return false;
    }
  }, [session, toast, updateSession]);


  const authContextValue: AuthContextType = {
    user: session?.user as SessionUser | null,
    login,
    register,
    logout,
    updateUserProfile,
    isLoading,
  };

  return (
    <AuthContext.Provider value={authContextValue}>
      {children}
    </AuthContext.Provider>
  );
}


export const AuthProvider = ({ children }: { children: React.ReactNode }) => {
  return (
    <SessionProvider>
      <AuthProviderContent>{children}</AuthProviderContent>
    </SessionProvider>
  );
};


export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error("useAuth harus digunakan di dalam AuthProvider");
  }
  return context;
};
